<template>
  <div>
   <login-top tip="注册supermall">
    <div slot="right" @click="$router.push('/login')" style="font-size:3.611vw;color:red">已有账号，去登录</div>
   </login-top>
   <login-text label="姓名" style="margin:4.167vw 0;" placeholder="请输入姓名" rule="^.{6,16}$" @inputchange="res=>model.name=res"></login-text>
   <login-text label="账号" placeholder='请输入账号' rule="^.{6,16}$"  @inputchange="res=>model.username=res"></login-text>
   <login-text label="密码" placeholder='请输入密码' type="password" rule="^.{6,16}$"  @inputchange="res=>model.password=res"></login-text>
   <login-btn btntext="注册" @registersubmit="registersubmit"></login-btn>
  </div>
</template>

<script>
import LoginTop from '../components/common/LoginTop'
import LoginText from '../components/common/LoginText'
import LoginBtn from '../components/common/LoginBtn'
export default {
  data(){
    return{
      model:{name:'',
username:'',
password:''}

    }
  },
components:{
  LoginTop,
  LoginText,
  LoginBtn
},
methods:{
 async registersubmit(){
   let rulg = /^.{6,16}$/
  if(rulg.test(this.model.name) && rulg.test(this.model.username) && rulg.test(this.model.password)){
  const res=await this.$http.post('/register',this.model)
  this.$msg.fail(res.data.msg)
  // setItem是往本地存储数据
  localStorage.setItem('id',res.data.id)
  localStorage.setItem('token',res.data.objtoken)
  setTimeout(()=>{
this.$router.push('/category')
  },1000)
  }else{
    this.$msg.fail('格式不正确,重新输入')
  }
},
// inputchange(content){
  
//   this.name=content
// }
},
mounted(){
 document.querySelector('body').setAttribute('style', 'background-color:#f7f7f7')
},
}

</script>

<style scoped>
body{
  background: cornsilk;
  width: 100%;
  height: 100%;
}
</style>